<template>
  <nav class="navbar is-dark" id="thing">
    <div class="container">
      <!-- Logo -->
      <div class="navbar-brand">
        <div id="logo-container" to="" class="navbar-item brand-text">
          <img id="logo" src="../assets/logo.png">
        </div>
        <!-- Mobile menu -->
        <div class="navbar-burger burger is-centered" data-target="navMenu"
          v-bind:class="{ 'is-active': showNav }"
          v-on:click="toggle">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
      <!-- Menu links -->
      <div id="navMenu" class="navbar-menu" v-bind:class="{ 'is-active': showNav }">
        <div class="navbar-start" v-on:click="condToggle">
          <router-link to="/" class="navbar-item" id="white"> Dashboard </router-link>
          <router-link to="/admin" class="navbar-item" id="white"> Admin </router-link>
          <router-link to="/alerts" class="navbar-item" id="white"> Alerts </router-link>
          <router-link to="/" class="navbar-item" id="white"> Logout </router-link>
          <div class="navbar-item" id="white">Currently logged in as: {{name}}</div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      showNav: false,
      name: ''
    }
  },
  mounted () {
    this.fetchData() // calls a function that fill's in our data for us
  },
  methods: {
    toggle () {
      // toggle between displaying and hiding mobile menu
      this.showNav = !this.showNav
    },
    condToggle () {
      // if user clicks on mobile menu, hide the menu
      if (this.showNav) {
        this.toggle()
      }
    }
  }
}
</script>

<style>
#white {
  color: white;
}
#thing {
  background: linear-gradient(to right, rgba(169, 21, 26, 255), rgba(221, 123, 50, 1));
}
nav.navbar {
  margin-bottom: 1rem;
}
.navbar-item.brand-text {
  font-weight: 300;
}
.navbar-item, .navbar-link {
  font-size: 15px;
  font-weight: 700;
}
#logo {
  max-height: 4rem;
}
</style>
