<template>
  <div class="hello" style="min-height:100vh">
    <span style="margin-bottom:20px;">
      <h1 style="font-size:50px; display:inline-block">Dashboard</h1>
      <b-button
        type="is-primary"
        style="background-color:#712844; margin-top:20px; margin-left:200px"
        @click="editAndSave"
      >
        <span v-if="!editMode">Edit</span>
        <span v-if="editMode">Save</span>
      </b-button>
      <b-button
        type="is-primary"
        style="background-color:#712844; margin-top:20px; margin-left:10px"
      >Add</b-button>
    </span>
    <div>
      <sortable
        v-for="(graph, index) in graphArray"
        v-model="dragData"
        :key="graph"
        :index="index"
        replace-direction="horizontal"
        @sortend="sortend($event, graphArray)"
        style="width:500px; display:inline-block; padding:5px"
      >
        <div>
          <b-icon
            v-if="editMode"
            icon="delete"
            type="is-danger"
            style="float:right; cursor:pointer; padding:10px"
            size="is-small"
            @click.native="deleteGraph(graph)"
          ></b-icon>
          <section class="info-tiles">
            <article class="tile is-child box">
              <line-chart
                :chart-data="getChartData('temperature')"
                :options="getChartOptions(graph)"
              ></line-chart>
            </article>
          </section>
        </div>
      </sortable>
    </div>
    <!-- <draggable v-model="graphArray" group="people" >
      <div
        v-for="graph in graphArray"
        :key="graph"
        style="width:500px; display:inline-block; padding:5px"
      >

      </div>
    </draggable>-->
  </div>
</template>

<script>
import LineChart from "@/components/ChartTemplates/LineChart";
import Sortable from "vue-drag-sortable";

// import { Role } from '@/_helpers/role.js';

export default {
  name: "Dashboard",
  data() {
    return {
      graphArray: [1, 2, 3, 4, 5, 6, 7],
      editMode: false,
      dragData: {},
      views: {
        "70884425-0cab-4840-a99e-ee397a9dbc71": {
          uuid: "70884425-0cab-4840-a99e-ee397a9dbc71",
          group: "6a7d3a03-5c06-4620-8325-583c8136b17f",
          authorized: "mcmaster_001",
          name: "Packet Count JHE",
          class: "line",
          field: "packets",
          fieldName: "Packets"
        }
      },
      groups: {
        "6a7d3a03-5c06-4620-8325-583c8136b17f": {
          uuid: "6a7d3a03-5c06-4620-8325-583c8136b17f",
          name: "McMaster University",
          authorized: ["mcmaster_001", "mcmaster_002"]
        }
      },
      dashboardData: [
        {
          uuid: "7290169e-9bdf-437b-8dd6-456b17341f05",
          group: "6a7d3a03-5c06-4620-8325-583c8136b17f",
          name: "McMaster University Dashboard",
          views: ["70884425-0cab-4840-a99e-ee397a9dbc71"],
          sizes: ["half"]
        }
      ]
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      // Fetch Groups
      fetch(process.env.VUE_APP_ENDPOINT + "group/list")
        .then(response => {
          if (response.ok) {
            return Promise.all([response.ok, response.json()]);
          } else {
            return Promise.all([response.ok, response.text()]);
          }
        })
        .then(response => {
          this.groups = {};
          const status = response[0];
          const data = response[1];
          if (!status) {
            this.$buefy.snackbar.open(data);
          }
          this.groups[data.current.uuid] = data.current.name;

          for (let i = 0; i < data.others.length; i++) {
            this.groups[data.others[i].uuid] = data.others[i].name;
          }
        });
      // Fetch Views
      fetch(process.env.VUE_APP_ENDPOINT + "views/list")
        .then(response => {
          if (response.ok) {
            return Promise.all([response.ok, response.json()]);
          } else {
            return Promise.all([response.ok, response.text()]);
          }
        })
        .then(response => {
          this.groups = {};
          const status = response[0];
          const data = response[1];
          if (!status) {
            this.$buefy.snackbar.open(data);
          }
          for (let i = 0; i < data.views.length; i++) {
            this.views[data.views[i].uuid] = data.views[i].name;
          }
        });
    },
    sortend(e, list) {
      const { oldIndex, newIndex } = e;
      this.rearrange(list, oldIndex, newIndex);
    },
    rearrange(array, oldIndex, newIndex) {
      if (oldIndex > newIndex) {
        array.splice(newIndex, 0, array[oldIndex]);
        array.splice(oldIndex + 1, 1);
      } else {
        array.splice(newIndex + 1, 0, array[oldIndex]);
        array.splice(oldIndex, 1);
      }
    },
    editAndSave() {
      // this.editMode = !this.editMode;
      this.$router.push('/dashboard/list')
    },
    deleteGraph(graph) {
      for (let i = 0; i < this.graphArray.length; i++) {
        if (this.graphArray[i] === graph) {
          this.graphArray.splice(i, 1);
        }
      }
    },
    getChartData(type) {
      return {
        labels: ["1", "2"],
        datasets: [
          {
            label: "Test",
            backgroundColor: "#2980b9",
            borderColor: "#2980b9",
            data: [10, 20],
            fill: false
          }
        ]
      };
    },
    getChartOptions(type) {
      return {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: type,
          fontSize: 30
        },
        legend: {
          display: false
        },
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        scales: {
          xAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: "Time"
              }
            }
          ],
          yAxes: [
            {
              scaleLabel: {
                display: true,
                labelString: "Y-Axis"
              }
            }
          ]
        }
      };
    }
  },
  components: {
    LineChart,
    Sortable
  }
};
</script>

